<template>
  <el-dialog
      :title="title"
      :visible.sync="dialogVisible2"
      :before-close="handleClose"
      width="80%">
    <div>
      <el-form :model="asset">
        <el-row>
          <el-col :span="20">
            <el-form-item label=":" prop="id">
              {{ asset.id }}
            </el-form-item>
          </el-col>
        </el-row>
        <el-row>
          <el-col :span="20">
            <el-form-item label=":" prop="assetName">
              {{ asset.assetName }}
            </el-form-item>
          </el-col>
          <el-col :span="6">
            <el-form-item label=":" prop="statusName">
              {{ asset.statusName }}
            </el-form-item>
          </el-col>
        </el-row>
        <el-row>
          <el-col :span="6">
            <el-form-item label=":" prop="brand">
              {{ asset.brand }}
            </el-form-item>
          </el-col>
          <el-col :span="6">
            <el-form-item label=":" prop="type">
              {{ asset.type }}
            </el-form-item>
          </el-col>
          <el-col :span="6">
            <el-form-item label=":" prop="price">
              {{ asset.price }}
            </el-form-item>
          </el-col>
        </el-row>
        <el-row>
          <el-col :span="10">
            <el-form-item v-show="asset.remark&&asset.remark!=''" label=":" prop="remark">
              {{ asset.remark }}
            </el-form-item>
          </el-col>
        </el-row>
      </el-form>
      <template v-if="asset.adv">
        <el-form v-show="asset.type==''" :model="asset.adv">
          <el-row>
            <el-col :span="6">
              <el-form-item label="G:" prop="memory">
                {{ asset.adv.memory }}
              </el-form-item>
            </el-col>
            <el-col :span="6">
              <el-form-item label=":" prop="screenSize">
                {{ asset.adv.screenSize }}
              </el-form-item>
            </el-col>
          </el-row>
        </el-form>
        <el-form v-show="asset.type==''" :model="asset.adv">
          <el-row>
            <el-col :span="6">
              <el-form-item label=":" prop="nums">
                {{ asset.adv.nums }}
              </el-form-item>
            </el-col>
            <el-col :span="6">
              <el-form-item label=":" prop="type">
                {{ asset.adv.type }}
              </el-form-item>
            </el-col>
          </el-row>
        </el-form>
        <el-form v-show="asset.type==''" :model="asset.adv">
          <el-row>
            <el-col :span="6">
              <el-form-item label="cpu:" prop="cpu">
                {{ asset.adv.cpu }}
              </el-form-item>
            </el-col>
            <el-col :span="6">
              <el-form-item label="G:" prop="memory">
                {{ asset.adv.memory }}
              </el-form-item>
            </el-col>
          </el-row>
        </el-form>
        <el-form v-show="asset.type==''" :model="asset.adv">
          <el-row>
            <el-col :span="6">
              <el-form-item label=":" prop="precision">
                {{ asset.adv.precision }}
              </el-form-item>
            </el-col>
            <el-col :span="6">
              <el-form-item label=":" prop="distance">
                {{ asset.adv.distance }}
              </el-form-item>
            </el-col>
            <el-col :span="6">
              <el-form-item label=":" prop="methods">
                {{ asset.adv.methods }}
              </el-form-item>
            </el-col>
          </el-row>
        </el-form>
        <el-form>
          <el-row v-show="urlList&&urlList.length>0">
            <el-form-item label=":"></el-form-item>
          </el-row>
          <el-row>
            <template v-for="url in urlList">
              <el-col :span="3">
                <el-form-item>
                  <el-image
                      style="width: 130px; height: 130px"
                      :src="url"
                      :preview-src-list="urlList">
                  </el-image>
                </el-form-item>
              </el-col>
            </template>
          </el-row>
        </el-form>
      </template>
    </div>
    <span slot="footer" class="dialog-footer">
        <el-button @click="$emit('close')"> </el-button>
  </span>
  </el-dialog>
</template>

<script>
export default {
  name: "AssetDetail",
  props: ['asset', 'title', 'urlList', 'dialogVisible2'],
  methods: {
    handleClose() {
      this.$emit('close');
    }
  }
}
</script>

<style scoped>

</style>